.filter-btn {
    display: inline-block;
    color: var(--heading-color);
    background: none;
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 0.85rem;
    font-weight: 600;
    letter-spacing: 0.4px;
    margin-left: 1.3rem;
    font-family: inherit;
    text-transform: uppercase;
    transition: 0.3s color;
}

.filter-btn.mixitup-control-active {
    color: var(--main-color-1);
}

/* --------------- 1000px Screens --------------- */
@media (max-width: 1000px) {
    .filter-btn {
        font-size: 0.7rem;
    }
}

/* --------------- 500px Screens --------------- */
@media (max-width: 500px) {
    .filter-btn {
        margin-top: 1rem;
    }
}
