.logo {
    margin-right: 1.5rem;
    font-family: 'Abril Fatface', cursive;
    font-size: 1.5rem;
    color: var(--heading-color);
    letter-spacing: 1px;
    transition: 0.3s;
}

.logo span {
    color: var(--main-color-1);
}

/* --------------- 1000px Screens --------------- */
@media (max-width: 100px) {
    .logo {
        font-size: 1.45rem;
    }
}
