header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    transition: 0.3s;
}

header.scrolled {
    background-color: var(--bg-color-transparent);
    box-shadow: 0 5px 20px 0.1px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(15px);
}

nav.container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

header .logo {
    height: 120px;
    line-height: 120px;
}

header.scrolled .logo {
    height: 80px;
    line-height: 80px;
}

/* --------------- 1000px Screens --------------- */
@media (max-width: 1000px) {
    header .logo {
        height: 100px;
        line-height: 100px;
    }
}

/* --------------- 768px Screens --------------- */
@media (max-width: 768px) {
    header nav {
        justify-content: space-between;
    }

    header {
        height: 505px;
        max-height: 100px;
        overflow: hidden;
    }

    header.scrolled {
        height: 485px;
        max-height: 80px;
    }

    body.open header {
        max-height: 505px;
        background-color: var(--bg-color-2);
    }

    body.open header.scrolled {
        max-height: 485px;
    }
}
