.skills {
    overflow: hidden;
}

.skills-box {
    background-color: var(--bg-color-2);
    padding: 5rem 6rem;
    border-radius: 20px;
    width: 100%;
    position: relative;
    transition: 0.3s background-color;
}

.box-desc {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center;
    column-gap: 2rem;
}

.skills-wrap {
    display: grid;
    grid-template-columns: repeat(4, 150px);
    justify-content: space-between;
    margin-top: 2rem;
    row-gap: 4rem;
}

@keyframes progress {
    to {
        stroke-dashoffset: var(--target);
    }
}

.skills .dots {
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(-50%, -50%);
    z-index: -1;
}

.skills .square {
    position: absolute;
    left: 100%;
    top: 75%;
    transform: translate(-50%, -50%);
}

/* --------------- 1000px Screens --------------- */
@media (max-width: 1000px) {
    .skills-box {
        padding: 4rem;
    }
}

/* --------------- 768px Screens --------------- */
@media (max-width: 768px) {
    .skills-wrap {
        grid-template-columns: repeat(2, 150px);
        row-gap: 1rem;
    }

    .box-desc {
        grid-template-columns: 1fr;
        justify-items: flex-start;
    }
}

/* --------------- 500px Screens --------------- */
@media (max-width: 500px) {
    .skills-box {
        padding: 3rem 2.5rem;
    }

    .skills-wrap {
        grid-template-columns: 1fr;
        row-gap: 1.5rem;
    }
}
